.calendar {
    width: 7 * ($days-width + $calendar-gap) + $padding-days;
    display: flex;
    gap: 1px;
    flex-wrap: wrap;
    padding: 10px;

  &__number {
    transition-duration: $transition-duration;
    display: flex;
    justify-content: center;
    width: $days-width;
    height: $days-heigth;
    border: 1px solid $days-border-color;
    background: $days-bgcolor;

    &:hover {
      transform: translateY(-20px);
      cursor: pointer;
      background: $days-hover-bgcolor;
    }
  }
}
